// //  axios 
import ajax from 'axios'
// axios
const axios = ajax.create({
 baseURL: 'http://127.0.0.1:3007' // 
})

// 
// const baseURL = 'http://127.0.0.1:3007'; // 

// , 
export default ({ url, method = 'GET', params, data, headers }) => {
 return axios({ 
   url: url,
   method: method,
   params: params,
   data: data,
   headers: headers
 })
}

